}

.lds-spinner div:after {
  content: " ";
  background: #fff;
  border-radius: 20%;
  width: 6px;
  height: 18px;
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
}

.lds-spinner div:first-child {
  animation-delay: -1.1s;
  transform: rotate(0);
}

.lds-spinner div:nth-child(2) {
  animation-delay: -1s;
  transform: rotate(30deg);
}

.lds-spinner div:nth-child(3) {
  animation-delay: -.9s;
